@import 'styles/breakpoints';

.container {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  
  .titulo {
    color: white;
    font-size: 60px;
    font-weight: 500;
    margin-bottom: 20px;
  }
  
  .descricao {
    color: white;
    font-size: 25px;
    font-weight: 400;
  }


  
@media screen and (max-width: 1400px) {

  .container{
    height: 200px;
    padding: 100px 520px 0 0;
  
  }




}


@media screen and (max-width: $mobile-sm){
  .container{
    display: flex;
    flex-direction: column;
    padding: 0;
    
  }
}